<template>
  <div id="browser">
    <div id="tabBar">
      <div class="tabs">
        <div class="tabGroup" v-for="(tab, key) in tabs">
          <button :class="activeOrNot(key)"
                  v-on:click="updateCurrentTab(key)"
                  v-bind:id="'tab_' + key">
                  {{tab.title}}</button><button @click.stop="closeTab(key)" class="closeTab">x</button>
        </div>
      </div>
        <button class="newTab" v-on:click="newTab()">+</button>
    </div>
    <div @dragover.prevent @drop="onDrop" v-for="(content, key) in tabs" id="tabContent" v-if="currentTab == key">
      <p>{{content.content}}</p>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      currentTab: 0,
      tabs: [{title:"Yeni Sekme 1", content: "content 1"}]
    }
  },
  methods: {
    newTab() {
      let countTab = parseInt(this.tabs.length)+1;
      this.tabs.push({title: "Yeni Sekme " + countTab, content: "content" + countTab})
      this.updateCurrentTab(this.tabs.length - 1) 
    },
    closeTab(id) {
      if (id == this.currentTab) {
        let newid = this.currentTab-1;
        if (newid < 0)
          newid = 0
        this.updateCurrentTab(newid) 
      }
      if (this.currentTab == this.tabs.length - 1) {
        this.updateCurrentTab(this.currentTab-1) 
      }
      this.$delete(this.tabs, id)
      this.saveSession()
    },
    updateCurrentTab(id) {
      this.currentTab = id
    },
    activeOrNot(key) {
      if (key == this.currentTab) {
        return 'tab active'
      }
      return 'tab'
    },
    onDrop(e){
      e.stopPropagation()
      e.preventDefault()
      let fileInfo = e.dataTransfer.files[0]
      let changeVal = this.tabs[this.currentTab];
      let fileReader = new FileReader();

      if (typeof fileInfo === "undefined") {
        alert("Sadece Dosyalar!!")
        return 0
      } else if (!fileInfo.type.match('text.*')) {
          alert("Yalnizca metin belgeleri!")
          return 0
      }

      changeVal.title = fileInfo.name; //method 1
      fileReader.onload = (f) => {
        this.$set(changeVal, "content", f.target.result) //method 2
        this.saveSession()
      }
      fileReader.readAsText(fileInfo)
    },
    saveSession() {
      localStorage.currentTab = this.currentTab
      localStorage.tabs = JSON.stringify(this.tabs)
    }
  },
  mounted() {
    if (localStorage.currentTab && localStorage.tabs) {
      this.tabs = JSON.parse(localStorage.tabs)
      this.currentTab = localStorage.currentTab
    }
  },
  watch: {
    currentTab() {
      localStorage.currentTab = this.currentTab
      localStorage.tabs = JSON.stringify(this.tabs)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#browser {
  width: 600px;
}
#tabBar {
  background-color: #dee1e6;
  height: 36px;
  display: flex;
}
.tabs {
  display: inline;
  margin-left: 2px;
}
.tabGroup {
  display: inline;
}
.tab{
    color: black;
    background-color: #dee1e6;
    border: 0.4px solid #989393;
    border-radius: 5px 5px 0 0;
    border-bottom: 0px;
    width: 127px;
    height: 34px;
    font-size: 15px;
    font-family: serif;
    margin-top: 2px;
    display: inline;
    margin-left: 5px; 
    overflow: hidden;
    white-space: nowrap;
}
.tab.active {
  background-color: white; 
}
.closeTab {
    border: 0px;
    background: transparent;
    margin-left: -20px;
    height: 20px;
    display: inline;
}
.closeTab:hover {
  background: #bbbcbdab;
  border-radius: 28px;
}
.newTab{
    color: black;
    background-color: transparent;
    border: 0px;
    width: 30px;
    height: 31px;
    font-size: 23px;
    display: inline;
    margin-top: 3px;
    margin-left: 3px;
}
.newTab:hover{
  background: lightgray;
  border-radius: 15px;
}
#tabContent {
  margin-top: -15px;
  padding-top: 7px;
  padding-left: 5px;
  border: 1px solid #dee1e6;
  word-wrap: break-word;
}
input, button {
 outline:none;
}
</style>



